.navbar {
    display: flex;
    justify-content: space-between;
    height: 60px;
    background-color: rgb(29, 161, 242);
    padding: 0 10px;

    h2 {
        display: flex;
        align-self: center;
        color: white;
    }

    .fa-icon {
        color: white;
        margin-left: 5px;
    }

  &__profile{
      display: flex;
      justify-content: flex-end;
      align-items: center;
      height: 100%;
      position: relative;
      cursor: pointer;

      p {
          display: flex;
          align-self: center;
          color: white;
      }

      img {
          display: flex;
          color: white;
          align-self: center;
          margin-right: 5px;
          border-radius: 50%;
      }

      .options{
        position: absolute;
        top: 50px;
        border-radius: 5px;
        padding: 0 5px;
        right: 5px;
        border: 1px solid #d9d9d9;
        background-color: white;
        z-index: 15;

        p {
            margin: 10px 5px;
            color: black;
        }
      }
  }
}