.creator {
    justify-content: flex-end;
}


.message {
    display: flex;
    padding-right: 5px;
    text-align: left;

    p {
        word-wrap: break-word;
        word-break: break-word;
    }

    &>div {
        border-radius: 20px;
    }

    .owner {
        background-color: rgb(29, 161, 242);
        color: white;
        padding: 10px;
    }

    .other-person {
        background-color: #f5f6f8;
        padding: 10px;
    }

    .dots {
        margin: -5px 0 3px 0;
    }

    img {
        max-width: 200px;
    }
}